// HEADER :: START

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;

  &--portfolio {
    background-color: $c-blackPearl;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 100px;
    @include respond(m_m) {
      min-height: 70px;
    }
  }
}

// HEADER :: END